









:root {
  --body-background-color: #f6f6f6;
  --body-secondary-color: #fff;
  --text-color: rgba(17, 17, 17, .6);
  --text-secondary-color: #111;
  --text-fourth-color: #4f2ee8;
}

body {
  color: var(--text-secondary-color);
  background-color: var(--body-background-color);
  font-family: DM Sans, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.33;
  transition: all .5s;
}

.container {
  max-width: 375px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

